import { createStore } from "redux";

const initialState = {
  sidebarShow: "responsive",
  isLoggedIn: false,
  id: "",
  username: "",
  Dept_ID: "",
  email: "",
  collegeID: "",
  firstName: "",
  middleName: "",
  lastName: "",
  phone_no: "",
  college_name: "",
  token: "",
  Dept_Name: "",
  user_roll:""
};

const changeState = (state = initialState, { type, ...rest }) => {
  switch (type) {
    case "set":
      return { ...state, ...rest };
    case "login":
      return { ...state, ...rest };
    default:
      return state;
  }
};

const store = createStore(changeState);
export default store;
